.outerdiv {
  background: white;
  min-height: 100vh;
  width: 90%;
  margin: 35px auto;
  border: 1px solid #d9d7d7;
  box-shadow: -2px 7px 14px -12px rgb(0 0 0 / 75%);
}

.companybar {
  /* height: 5%; */
  height: 40px;
  border-bottom: 1px solid #d9d7d7;
  padding: 10px;
}

.wrapper {
  display: flex;
}

.contentdiv {
  height: 100%;
  width: 100%;
  border-right: 1px solid #d9d7d7;
}

.noticeHeader {
  cursor: pointer;
  color: #fff;
  padding: 20px;
  border-radius: 8px;
  transition: background-color 0.3s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #333;
  margin-top: 40px;
}

.hero {
  height: 160px;
  background: url("/companydetailbackground.jpg");
}

.companylogo {
  width: 108px;
  height: 92px;
  border-radius: 2px;
  background: white;
  margin-top: -40px;
  margin-left: 40px;
  border: 2px solid #fb7b4a;
  box-shadow: -2px 5px 14px -12px rgb(0 0 0 / 75%);
}

.typetag {
  width: fit-content;
  padding: 3px 5px;
  border: 1px solid #fb7b4a;
  border-radius: 500px;
  font-size: 12px;
  color: #fb7b4a;
}

.parent {
  display: grid;
  grid-template-columns: 1fr 3fr;
  /* grid-template-rows: repeat(4, 5fr); */
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  padding: 10px;
  border-bottom: 1px solid #d2d4dc;
}

.parenttag {
  font-weight: bold;
}

.file {
  display: flex;
  height: 50px;
  width: fit-content;
  border: 1px solid #fb7b4a;
  border-radius: 3px;
}

.fileicon {
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-right: 1px solid #fb7b4a;
}

.filetitle {
  padding: 5px;
  border-right: 1px solid #d2d4dc;
  width: 170px;
  overflow: hidden;
}

.downloadicon {
  width: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.eligiblewrapper {
  background: #ff4e1526;
  border-radius: 5px;
  padding: 10px;
}

.companycontentwrapper {
  padding: 0 50px;
}

@media screen and (max-width: 760px) {
  .contentdiv {
    width: 100%;
  }

  .parent {
    grid-template-columns: 2fr 3fr;
    grid-column-gap: 10px;
  }
}

@media screen and (max-width: 1000px) {
  .wrapper {
    /* height:95%; */
    /* height:800px; */
    display: block;
  }
  .resumediv {
    width: 100%;
  }

  .contentdiv {
    width: 100%;
  }
}

@media screen and (max-width: 500px) {
  .companycontentwrapper {
    padding: 0 20px;
  }
  .outerdiv {
    width: 100%;
  }
}
